import React from 'react'

class FriendFrame extends React.Component {
    
    constructor (props) {
        super(props)
        this.state = {
            email: this.props.data.email,
            name: this.props.data.name
        }
        this.clickEvent = this.clickEvent.bind(this)
        this.accept = this.accept.bind(this)
        this.decline = this.decline.bind(this)
    }
    
    render () {
        if (this.props.data.status === 'pending') {
            return (
                <div className='row gap-1 m-1 border-bottom border-top border-success border-curve-10 clickable' onClick={this.clickEvent}>
                    <div className='col-7 d-flex align-items-center pt-1'>
                        <h5>{this.props.data.name}</h5>
                    </div>
                    <div className='col-2 d-grid gap-2 d-md-flex pt-1'>
                        <button className='btn btn-primary btn-sm h-29-w-29' onClick={this.accept}>+</button>
                        <button className='btn btn-primary btn-sm h-29-w-29' onClick={this.decline}>-</button>
                    </div>
                </div>
            )
        } else {            
            return (
                <div className='row gap-1 m-1 border-bottom border-top border-success border-curve-10 clickable' onClick={this.clickEvent}>
                    <div className='col-9 d-flex align-items-center pt-1'>
                        <h5>{this.props.data.name}</h5>
                    </div>
                    <div className='col-1 d-flex align-items-center pt-1'>
                        <h6>{this.props.data.messageCount}</h6>
                    </div>
                </div>
            )
        }
    }

    clickEvent() {
        this.props.changeCurrentRecipient ({
            email: this.state.email,
            name: this.state.name
        })
    }

    accept () {
        this.props.accept(this.state.email)
    }

    decline () {
        this.props.decline(this.state.email)
    }
}

export default FriendFrame