import React from 'react'
import RecipientList from './RecipientList'
import MessageRoom from './MessageRoom'
import socketIOClient from 'socket.io-client'
import reactCookie from 'react-cookies'
import {Redirect} from 'react-router-dom'

class MessGallery extends React.Component {
    
    constructor (props) {
        super(props)
        this.sock = socketIOClient('http://localhost:5000', {query: {UID: reactCookie.load('UID')}})
        if (reactCookie.load('UID') === undefined) {
            this.state = {
                loggedin: false,                
            }
        } else {
            this.state = {
                account: {},
                loggedin: true,
                current_recipient: {},
                messages: {
                    'friend_1@gmail.com': {
                        email: 'friend_1@gmail.com',
                        name: 'friend_1',
                        message: [],
                        messageCount: null,
                        status: 'pending'
                    }
                }
            }
        }
        this.setCallbacks = this.setCallbacks.bind(this)
        this.getAccountName = this.getAccountName.bind(this)
        this.changeCurrentRecipient = this.changeCurrentRecipient.bind(this)
        this.sendMessage = this.sendMessage.bind(this)
        this.addFriend = this.addFriend.bind(this)
        this.refreshRecipientList = this.refreshRecipientList.bind(this)
        this.accept = this.accept.bind(this)
        this.decline = this.decline.bind(this)
    }

    render() {
        if (this.state.loggedin) {
            return (
                <div className='message-gallery border-curve-10 row'>
                    <RecipientList changeCurrentRecipient={this.changeCurrentRecipient} addFriend={this.addFriend} account={this.state.account} friendList={this.state.messages} accept={this.accept} decline={this.decline}></RecipientList>
                    <MessageRoom sendMessage={this.sendMessage} recipientName={this.state.current_recipient.name} messages={this.state.messages[this.state.current_recipient.email]}></MessageRoom>     
                </div>
            )
        } else {
            return (
                <Redirect to='/signup'></Redirect>
            )
        }
    }

    componentDidMount() {
        // this.sock.on('message', (data) => {
        //     console.log(data)
        // })
        this.setCallbacks()
        this.getAccountName()
        this.refreshRecipientList()
    }

    setCallbacks() {
        this.sock.on('refresh_recipient_list', (data) => {
            this.refreshRecipientList()
        })
    }

    getAccountName() {
        const req = new XMLHttpRequest()
        req.addEventListener('load', () => {
            this.setState({account: JSON.parse(req.responseText)})
        })
        req.open('GET', 'http://localhost:4000/account')
        req.send()
    }

    refreshRecipientList () {
        console.log('works')
        const req = new XMLHttpRequest()
        req.addEventListener('load', () => {
            // friend list here
            const result = JSON.parse(req.responseText)
            console.log(result)
            const temp = {}
            for (var i = 0; i < result.length; i++) {
                temp[result[i].email] = result[i]
                temp[result[i].email].message = []
            }
            this.setState({messages: temp})
        })
        req.open('GET','http://localhost:4000/friend')
        req.send()
    }

    changeCurrentRecipient (o) {
        this.setState({current_recipient: o})
    }

    sendMessage (message) {
        const date = new Date()
        const current_time = date.getHours() + ':' + date.getMinutes()
        const data = {
            message: message,
            recipient: this.state.current_recipient.email,
            time: current_time,
            type: 'outgoing'
        }

        let temp = this.state.messages
        temp[data.recipient].message.push(data)
        this.setState({messages: temp})

        this.sock.emit('message', data)
    }

    addFriend (email) {
        this.sock.emit('add_friend', {
            email: email
        })
        this.refreshRecipientList()
    }

    accept (email) {
        this.sock.emit('accept_request', {
            requestFrom: email,
            requestTo: this.account.email
        })
        this.refreshRecipientList()
    }

    decline (email) {
        this.sock.emit('decline_request', {
            requestFrom: email,
            requestTo: this.account.email
        })
        this.refreshRecipientList()
    }
}

export default MessGallery;