import React from 'react'
import FriendFrame from './FriendFrame'

class List extends React.Component {
    

    render() {
        let list = []
        if (this.props.friendList !== undefined) {
            for (let [index, value] of Object.entries(this.props.friendList)) {
                console.log(value)
                list.push(<FriendFrame key={index} data={value} accept={this.props.accept} decline={this.props.decline}></FriendFrame>)
            }
        }
        console.log(list)
        return (
            <div className='d-flex row h-80-percent-oflow'>
                <div>
                    {list}
                </div>
            </div>
        )
    }

}


export default List